body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}
body {
  font-family: 'Montserrat', sans-serif;
  background: url(/Star-Wars-Friends/static/media/stars.6494ab5b.png);
  background-color: black;
  overflow-y: hidden; 
}

@font-face {
	font-family: 'STAR WARS FONT';
	src: url(/Star-Wars-Friends/static/media/Starjedi.cc3019aa.ttf);
}

.App-logo {
  margin: 20px;
}

.displayContent {
	text-align:center;
	opacity:0;
	-webkit-animation:opacity 3s 1 forwards;
	        animation:opacity 3s 1 forwards;
}

.App-header {
  padding: 20px;
  max-height: 280px;
}

@-webkit-keyframes opacity{
  to {opacity:1}
}

@keyframes opacity{
  to {opacity:1}
}

@media only screen and (max-width: 1400px) {
  body {
    font-size: 14px;
  }
}

@media only screen and (max-width: 1200px) {
  body {
    font-size: 12px;
  }
}

@media only screen and (max-width: 800px) {
  .App-logo{
    height: 125px;
  }
  .App-logoContainer{
    width: 100%;
    display: flex;
    justify-content: center;
   }
}

.planetPhoto {
  width: 60%;
  margin: 0 auto;
  position: relative;
}

img.magnifier {
  position: absolute;
  max-width: 30px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0;
  z-index: 2;
}

div.planetPhoto:hover > img.magnifier {
  opacity: 1;
}

.Popup-questionMark {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 1;
  color: white;
  font-weight: bold;
  font-size: 30px;
}

div.planetPhoto:hover > .Popup-questionMark {
  opacity: 0;
}

@media only screen and (max-width: 800px) {
 .planetPhoto {
    width: 100%;
  }
  img.magnifier {
    opacity: 1;
  }
  .Popup-questionMark {
    display: none;
  }
}
.PopupPlanet-style {
  background-color: transparent;
  background-size: cover;
  color: black;
  font-weight: bold;
}

hr {
  border-bottom: 1px solid transparent;
  border-image: linear-gradient(to left, transparent, #222, transparent);
  border-image-slice: 1;
}

@media only screen and (max-width: 800px) {
  .PopupPlanet-style {
    position: absolute !important;
    left: 100% !important;
    top: 0 !important;
    width: 100% !important;
    height: 100%;
  }
  p {
    font-size: 2.5vw;
    margin-top: 15px;
  }
}
.Popup-covers {
  display: flex;
  flex-flow: row wrap;
}

.Popup-coverOn,
.Popup-coverOff {
  width: 30%;
  margin: 1%;
  align-self: center;
}

.Popup-coverOff {
  -webkit-filter: grayscale(100%) blur(1px);
          filter: grayscale(100%) blur(1px);
}

@media only screen and (max-width: 800px) {
  .Popup-covers{
    justify-content: space-around;
  }
}
.cardsStyle {
  background-image: linear-gradient(240deg, #333, #000);
  border: 1px solid white;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
  border-radius: 0.5em;
  display: inline-block;
  margin: 0.5em;
  padding: 1.3em;
}

.cardsStyle:hover,focus {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.cardsStyle h2 {
  background: -webkit-linear-gradient(#eee, #5e5e5e);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.Close-Popup {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}

.Popup-header {
  letter-spacing: 5px;
  max-width: 50vw;
  text-align: center;
  border-bottom: 2px solid transparent;
  border-image: linear-gradient(to left, #aaa, #222, #aaa);
  border-image-slice: 1;
  padding: 5px;
  text-transform: uppercase;
  font-weight: bold;
  background: #000000; /* fallback for old browsers */
  background: linear-gradient(to left, #434343, #000000);
}

.Popup-header h1 {
  font-size: 20px;
  background: -webkit-linear-gradient(#eee, #5e5e5e);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.Popup-container {
  display: flex;
  justify-content: space-between;
  background: #000000; /* fallback for old browsers */
  background: linear-gradient(to left, #434343, #000000);
  color: white;
  max-width: 50vw;
}

.Popup-planet,
.Popup-films {
  width: 35%;
}

.Popup-content {
  align-self: center;
}

.Popup-content ul {
  padding-left: 0;
  text-align: center;
}

.Popup-content li {
  list-style-type: none;
  text-align: center;
  margin-left: 0;
  padding: 2px 0 1px 0;
}

@media only screen and (max-width: 800px) {
  .cardsStyle img {
    display: none;
  }
 .Popup-container {
    flex-flow: row wrap;
    align-items: center;
    left: 0;
    max-width: 75vw;
  }
  .Popup-header {
    max-width: 75vw;
  }
  .Popup-films {
    width: 100%;
  }
  .Popup-planet,
  .Popup-content {
    width: 50%;
  }
  .Popup-planet {
    position: relative;
  }
  .Popup-planet img {
    width: 80%;
  }
  .Popup-content ul {
    line-height: 1em;
    font-size: 0.9em;
  }
}
.lightsaberContainer {
  position: relative;
  max-width: 40%;
  display: flex;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

.loadingWord {
  font-family: STAR WARS FONT;
  position: absolute;
  font-size: 3.5em;
  left: 50%;
  top: -1em;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: transparent;
  background: linear-gradient(90deg, #741515, transparent 80%);
  background-size: 400% 400%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: changeColor 2s infinite linear;
          animation: changeColor 2s infinite linear;
}

.lightsaberDiv {
  align-self: center;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  background-color: white;
  -webkit-filter: blur(3px);
          filter: blur(3px);
  width: 0%;
  padding: 3px 0;
  margin-bottom: 2px;
  margin-left: -3%;
  box-shadow: 0 0 0 4px red, 0 0 2px 6px rgb(32, 6, 6);
  -webkit-animation: laser 2s infinite ease-out;
          animation: laser 2s infinite ease-out;
}

.lightsaberHilt {
  width: 30%;
  height: 100%;
  z-index: 1;
}

@-webkit-keyframes laser {
  0% {
    width: 0%;
  }
  100% {
    width: 70%;
  }
}

@keyframes laser {
  0% {
    width: 0%;
  }
  100% {
    width: 70%;
  }
}

@media only screen and (max-width: 1400px) {
  .lightsaberDiv {
    -webkit-filter: blur(2px);
            filter: blur(2px);
    padding: 1px 0;
    box-shadow: 0 0 0 2px red;
    margin-bottom: 0;
  }
}

@media only screen and (max-width: 1200px) {
  .lightsaberDiv {
    -webkit-filter: blur(2px);
            filter: blur(2px);
    padding: 2px 0;
    box-shadow: 0 0 0 3px red;
  }
}

@media only screen and (max-width: 800px) {
  .lightsaberContainer {
    max-width: 80%;
    display: flex;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  .lightsaberDiv {
    -webkit-filter: blur(1px);
            filter: blur(1px);
    padding: 1.5px 0;
    box-shadow: 0 0 0 2px red;
    margin-bottom: 1px;
  }
}

.searchSettings {
  display: flex;
  justify-content: center;
  margin-bottom: 15px
}

.filterSortContainer {
  position: relative;
  display: flex;
}

.sortButton {
  cursor: pointer;
  width: 2.5em;
  border: 0 !important;
  border-radius: .25em 0 0 .25em;
  background: #5a7b9b;
}

.sortButton:hover {
  background:#6b8cac;
}

.sortButton:focus {
  outline: none;
}

.searchContainer {
  position: relative;
  width: 15em;
  max-width: 250px;
}

.searchInput {
  width:15em;
  height: 2em;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 2px solid #2c3e50;
  border-radius: 5px;
  background: #2c3e50;
  color: #FFD300;
  margin:0 15px;
  transition: all .15s ease;
}

.searchInput:focus {
  outline: none;
}

.searchInput:focus + .borderBottom-Input {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  z-index:1;
}

.searchInput::-webkit-input-placeholder {
  color:#bbbbbb;
}

.searchInput::-ms-input-placeholder {
  color:#bbbbbb;
}

.searchInput::placeholder {
  color:#bbbbbb;
}

.borderBottom-Input {
    margin:0 15px;
    position: absolute;
    width:15em;
    bottom: 0;
    left: 0;
    height: 2px;
    background: #FFD300;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    transition: all .15s ease;
}


@media only screen and (max-width: 800px) {
  .searchContainer{
    margin: 0 auto;
    align-self: center;
    width:100vw;
  }
  .searchInput, .borderBottom-Input{
    margin:0;
    width:100%;
  }
  .searchSettings{
    font-size: 18px;
    flex-flow: column-reverse;
    align-items: center;
  }
  .filterSortContainer{
    margin:10px auto 0 auto;
    width:90vw;
    font-size: 16px;
  }
}
select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background:rgb(44, 62, 80);
  background-image: none;
  cursor: pointer;
  color: #fff;
  padding: 0 0 0 .5em;
  margin: 0;
  width: 100%;
  height: 100%;
}

.select {
  position: relative;
  display: block;
  width: 20em;
  height: 2em;
  line-height: 2;
  background: #2c3e50;
  overflow: hidden;
  border-radius: 0 .25em .25em 0;
}

.select::after {
  content: '\25BC';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 0 1em;
  background: #34495e;
  pointer-events: none;
  transition: .25s all ease;
}

.select:hover::after {
  color: #FFD300;
}

.select:active::after {
  color: #FFD300;
}

.select:focus::after {
  color: #FFD300;
}

@media only screen and (max-width: 800px) {
  .select{
    align-self: center;
    width:100%;
  }
}
.scroll::-webkit-scrollbar {
    width: 18px;
}

.scroll::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(255,255,255,0.5); 
    border-radius: 10px;
}

.scroll::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(255,255,255,0.8); 
}

.scroll {
	border-top: 2px solid transparent;
	border-image: linear-gradient(to left, #aaa, #222, #aaa);
	border-image-slice: 1;
}
